import {
	getPageLayout
} from '@/api/userApi.js'
export default {
	name: 'StrictlyGoods',
	props: {
		// query: {
		//   type: Array,
		//   default: []
		// }
	},
	data() {
		return {
			goodsObj: '',
			list: []
		}
	},
	mounted() {
		this.getPageLayoutList()
	},
	methods: {
		goGoodsDetail(item) {
			if(item.type == 1){
				uni.navigateTo({
					url:'/pages/common/webview/webview?url='+item.url
				})
			} else {
				uni.navigateTo({
					url:'/pages/order/goodsDetail/goodsDetail?shopId='+item.shopId + '&goodsId='+ item.id
				})
			}
			
		},
		// 获取严选好物列表
		getPageLayoutList() {
			let data = {
				parentId: '1-5'
			}
			getPageLayout(data).then(res => {
				if (res.code === '1000') {
					let list = res.data.list
					this.list = res.data.list
					list.forEach(item => {
						if (item.componentType === 5) {
							item.goodsDetailRespList.forEach(item => {
								let arr = item.valueAddr.split('-');
								item.valueAddr = arr[arr.length-1]
							})
							this.goodsObj = item
						}
					})
				}
			})
		},
		toForeach(item) {
			let arr = []
			if (item.list) {
				item.list.forEach(item => {
					arr.push(item)
				})
			}
			return arr
		}
	}
}
